html, body {
  box-sizing: border-box;
  max-width: 100%;
  min-width: 320px;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  color: #777777;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px; }

a {
  text-decoration: none;
  margin: 0;
  padding: 0;
  color: #777777;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px; }

*,
*::before,
*::after {
  box-sizing: border-box; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

body {
  background-color: #ffffff; }

.gray {
  background-color: #777777; }

.col {
  float: left; }

.h-100,
.h-50 {
  padding-top: 15px;
  padding-bottom: 15px; }
  .h-100:nth-child(1),
  .h-50:nth-child(1) {
    padding-top: 0; }
  .h-100:nth-last-child(1),
  .h-50:nth-last-child(1) {
    padding-bottom: 0; }

.h-100 {
  height: 100%; }

.h-50 {
  height: 50%; }

.w-100 {
  width: 100%; }

.clearfix::after {
  content: "";
  display: block;
  clear: both; }

.header {
  height: 156px; }

.header {
  width: 100%; }

.header__wrap {
  height: 155px;
  padding-top: 25px;
  padding-bottom: 25px;
  border-bottom: 1px solid dimgray; }

.header__sub-wrap {
  padding: 15px;
  height: 100%; }

.header__link {
  display: block;
  font-size: 14px;
  line-height: 22px;
  font-weight: 400;
  text-align: right;
  color: #ffffff; }

.index-page {
  padding-top: 20px;
  padding-bottom: 35px; }

.index-page__desc {
  padding: 20px;
  min-height: 470px;
  background-color: #dcdcdc; }
  .index-page__desc p:nth-child(1) {
    margin-top: 0; }
  .index-page__desc p:nth-last-child(1) {
    margin-bottom: 0; }

.index-page__cards-list {
  display: block;
  margin: 0;
  padding: 0;
  list-style: none;
  width: 100%; }

.index-page__block-7-wrap {
  float: left;
  height: 13%; }

.index-page__block-7 {
  background-color: #777777;
  height: 130px;
  margin-top: 20px; }

.index-page__block-5-wrap {
  margin-bottom: 30px; }

.index-page__sidebar-wrap {
  float: left;
  height: 87%; }

.block-5 {
  padding: 20px;
  background-color: #dcdcdc;
  min-height: 100%; }

.block-5__title {
  margin: 0;
  margin-bottom: 30px;
  padding: 0; }

.card {
  width: 20%;
  -webkit-box-flex: 0;
          flex-grow: 0;
  margin-bottom: 30px; }

.card__wrap {
  position: relative;
  width: 100%;
  padding: 20px;
  padding-top: 60px;
  min-height: 150px;
  background-color: #dcdcdc; }
  .card__wrap svg {
    position: absolute;
    top: 20px;
    right: 20px; }

.sidebar {
  color: #ffffff;
  background-color: #777777;
  padding: 20px; }

.sidebar__title {
  margin: 0;
  margin-bottom: 5px;
  padding: 0; }

.sidebar__desc {
  margin: 0;
  margin-bottom: 2px; }

.nav__list {
  list-style: none;
  margin: 0;
  padding: 0; }

.nav__link {
  color: #ffffff; }

.footer__wrap {
  padding-top: 36px;
  padding-bottom: 36px;
  border-top: 1px solid dimgray; }

.footer__list {
  padding: 20px;
  height: 100%;
  background-color: #777777; }
  .footer__list.list--num {
    -webkit-column-count: 2;
       -moz-column-count: 2;
            column-count: 2; }

.list {
  margin: 0;
  list-style: none; }
  .list--num {
    counter-reset: li; }
    .list--num .list__item {
      width: 50%;
      padding-left: 0; }
      .list--num .list__item::before {
        content: none; }

.list__item {
  position: relative;
  width: 100%;
  padding: 0;
  padding-left: 17px;
  margin: 0;
  color: #ffffff; }
  .list__item::before {
    content: '';
    width: 7px;
    height: 7px;
    position: absolute;
    left: 0;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    background-color: #ffffff; }

.list__item-num {
  width: 100%;
  padding-right: 25px;
  position: relative;
  color: #ffffff; }
  .list__item-num:before {
    content: counter(li);
    counter-increment: li;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    right: 0;
    width: 2em;
    text-align: right; }
